import React from 'react';

function Website(props) {
  const handleClick = () => {
    window.open(props.url, '_blank');
  };

  return (
    <button className="w-2/6 grid place-items-center p-2 border-4 rounded border-emerald-950 cursor-pointer" onClick={handleClick}>
      <div>
        <p className="text-emerald-400">{props.name}</p>
        <h1 className="text-center">{props.description}</h1>
      </div>
    </button>
  );
}

export default Website;
